/**
 * WordPress Dependencies
 */
import { useSelect } from "@wordpress/data";
import { useEffect } from "@wordpress/element";
/**
 * External Dependencies
 */
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
/**
 * Internal Dependencies
 */
import { locationFormSchema, } from "../create-and-update/schema";
import useLocationApi from "./useLocationApi";
import { stores } from "@/globalConstant";
/**
 * Custom hook to manage location form state and operations
 * Handles both create and edit modes with proper data loading and form initialization
 */
export const useLocationForm = ({ locationId }) => {
    const { getSingleLocation } = useLocationApi();
    // Access states from stores
    const { data: settingsData } = useSelect((select) => select(stores === null || stores === void 0 ? void 0 : stores.settings).getSettingsState(), []);
    const { singleLocation } = useSelect((select) => select(stores === null || stores === void 0 ? void 0 : stores.location).getLocationState(), []);
    // Map location data to form values
    const mapLocationToFormValues = (location) => {
        const defaultValues = settingsData;
        return {
            restaurants_name: (location === null || location === void 0 ? void 0 : location.restaurants_name) || "",
            location: (location === null || location === void 0 ? void 0 : location.location) || "",
            override_open_hours: location === null || location === void 0 ? void 0 : location.override_open_hours,
            override_online_ordering: location === null || location === void 0 ? void 0 : location.override_online_ordering,
            override_reservation: location === null || location === void 0 ? void 0 : location.override_reservation,
            override_pickup_schedule: (location === null || location === void 0 ? void 0 : location.override_pickup_schedule) || "0",
            override_delivery_schedule: (location === null || location === void 0 ? void 0 : location.override_delivery_schedule) || "0",
            // Handle open_hours which can be OpenHours or empty array
            open_hours: Array.isArray(location === null || location === void 0 ? void 0 : location.open_hours)
                ? defaultValues.open_hours
                : (location === null || location === void 0 ? void 0 : location.open_hours) || defaultValues.open_hours,
            pickup_schedule: (location === null || location === void 0 ? void 0 : location.pickup_schedule) || defaultValues.pickup_schedule,
            delivery_schedule: (location === null || location === void 0 ? void 0 : location.delivery_schedule) || defaultValues.delivery_schedule,
            slot_interval: (location === null || location === void 0 ? void 0 : location.slot_interval) || defaultValues.slot_interval,
            pickup_slot_interval: (location === null || location === void 0 ? void 0 : location.pickup_slot_interval) ||
                defaultValues.pickup_slot_interval,
            delivery_slot_interval: (location === null || location === void 0 ? void 0 : location.delivery_slot_interval) ||
                defaultValues.delivery_slot_interval,
            enable_pickup: location === null || location === void 0 ? void 0 : location.enable_pickup,
            enable_delivery: location === null || location === void 0 ? void 0 : location.enable_delivery,
            enable_reservation: location === null || location === void 0 ? void 0 : location.enable_reservation,
            pickup_minimum_order_amount: parseFloat(location === null || location === void 0 ? void 0 : location.pickup_minmum_order_amount) ||
                defaultValues.pickup_minimum_order_amount,
            pickup_prepare_time: parseFloat(location === null || location === void 0 ? void 0 : location.pickup_food_prepare_time) ||
                defaultValues.pickup_prepare_time,
            delivery_minimum_order_amount: parseFloat(location === null || location === void 0 ? void 0 : location.delivery_minimum_order_amount) ||
                defaultValues.delivery_minimum_order_amount,
            delivery_prepare_time: parseFloat(location === null || location === void 0 ? void 0 : location.delivery_prepare_time) ||
                defaultValues.delivery_prepare_time,
            reservation_minimum_guest: parseFloat(location === null || location === void 0 ? void 0 : location.reservation_minimum_guest) ||
                defaultValues.reservation_minimum_guest,
            reservation_maximum_guest: parseFloat(location === null || location === void 0 ? void 0 : location.reservation_maximum_guest) ||
                defaultValues.reservation_maximum_guest,
            reservation_advanced: parseFloat(location === null || location === void 0 ? void 0 : location.reservation_advanced) ||
                defaultValues.reservation_advanced,
            reservation_total_seat_capacity: parseFloat(location === null || location === void 0 ? void 0 : location.reservation_total_seat_capacity) ||
                defaultValues.reservation_total_seat_capacity,
            reservation_hold_time_late_guest: parseFloat(location === null || location === void 0 ? void 0 : location.reservation_hold_time_late_guest) ||
                defaultValues.reservation_hold_time_late_guest,
            reservation_booking_amount: parseFloat(location === null || location === void 0 ? void 0 : location.reservation_booking_amount) ||
                defaultValues.reservation_booking_amount,
            reservation_status: (location === null || location === void 0 ? void 0 : location.reservation_status) ||
                defaultValues.reservation_status,
            multiply_booking_amount_with_guests: (location === null || location === void 0 ? void 0 : location.multiply_booking_amount_with_guests) ||
                defaultValues.multiply_booking_amount_with_guests,
            reservation_enable_table_layout: (location === null || location === void 0 ? void 0 : location.reservation_enable_table_layout) ||
                defaultValues.reservation_enable_table_layout,
        };
    };
    // Initialize form with proper configuration
    const methods = useForm(Object.assign(Object.assign({ resolver: zodResolver(locationFormSchema) }, (locationId &&
        singleLocation && {
        values: mapLocationToFormValues(singleLocation),
    })), { 
        // Fallback default values for create mode
        defaultValues: settingsData }));
    // Fetch location data for edit mode
    useEffect(() => {
        if (!locationId)
            return;
        if (singleLocation && singleLocation.term_id === locationId)
            return; // Check if we have the correct location data
        getSingleLocation(locationId);
    }, [locationId]);
    // Determine mode
    const isEditMode = Boolean(locationId);
    const isLoading = isEditMode &&
        (!singleLocation || singleLocation.term_id !== locationId);
    return {
        // Form methods
        methods,
        // Convenience destructured methods
        handleSubmit: methods.handleSubmit,
        reset: methods.reset,
        // State
        isEditMode,
        isLoading,
        locationId,
        // Data
        settingsData,
        singleLocation,
    };
};
