/**
 * External Dependencies
 */
import { z } from "zod";
/**
 * Internal Dependencies
 */
import { numberField, scheduleSchema, settingsSchema, trueFalseField, } from "@/admin/features/settings/settingsSchema";
import { onboardSchema } from "@/admin/features/onboard/onboardSchema";
/**
 * Location Create/Edit Form Schema
 * Matches settings schema field names for compatibility
 */
export const locationFormSchema = settingsSchema.extend({
    // Required fields
    restaurants_name: z.string().min(1, "Restaurant name is required"),
    location: onboardSchema.shape.restaurant_location,
    // Toggle override fields
    override_open_hours: trueFalseField.optional(),
    override_online_ordering: trueFalseField.optional(),
    override_reservation: trueFalseField.optional(),
    // Open hours (when override is enabled) - matches settings schema
    open_hours: scheduleSchema.optional(),
    slot_interval: numberField.optional(),
    // Pickup schedule override
    override_pickup_schedule: trueFalseField.optional(),
    pickup_schedule: scheduleSchema.optional(),
    pickup_slot_interval: numberField.optional(),
    // Delivery schedule override
    override_delivery_schedule: trueFalseField.optional(),
    delivery_schedule: scheduleSchema.optional(),
    delivery_slot_interval: numberField.optional(),
    enable_pickup: trueFalseField.optional(),
    enable_delivery: trueFalseField.optional(),
    // Reservation fields (matching settings schema field names)
    enable_reservation: trueFalseField.optional(),
    reservation_enable_table_layout: trueFalseField.optional(),
});
